import ErmjBaseView from "./ErmjBaseView";
import ErmjPathHelper, { ErmjAudioConst } from "../data/ErmjPathHelper";
import ErmjRuleConst from "../data/ErmjRuleConst";
import ErmjMjStyleHelper from "../tool/ErmjMjStyleHelper";
import ErmjGameConst from "../data/ErmjGameConst";
import ErmjDriver from "../ErmjDriver";

export default class ErmjSettleView extends ErmjBaseView{
    private clickLimit = false;
    private maskNode: cc.Node;
    private contentNode: cc.Node;
    private winInfoView: WinInfoView;
    private loseInfoView: LoseInfoView;
    private listLayout: cc.Layout;
    private copyTypeItem: FanTypeView;
    private typeScrollView: cc.ScrollView;
    private typeItemList: FanTypeView[] = [];
    private nextIndex: number = 0;
    private winMjListNode: cc.Node;
    private winMjList: WinMjItemView[] = [];
    private actionNode: cc.Node;
    private timer: NodeJS.Timeout;
    private hideBtn: cc.Node;
    private isPressed: boolean = false;
    private upIcon: cc.Node;
    private downIcon: cc.Node;
    private rawPos: cc.Vec3;
    private winTypeSp: cc.Sprite;
    
    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.maskNode = this.getChild("mask");
        this.actionNode = this.getChild("action");
        ErmjGameConst.addCommonClick(this.node, "action/continueBtn", this.onContinueClick, this);
        ErmjGameConst.addCommonClick(this.node, "action/leaveBtn", this.onLeaveClick, this);

        this.contentNode = this.getChild("contentMask/content");
        this.winInfoView = new WinInfoView(this.getChild("contentMask/content/winNode"));
        this.loseInfoView = new LoseInfoView(this.getChild("contentMask/content/loseNode"));
        this.typeScrollView = this.getComponent("contentMask/content/typeScrollView", cc.ScrollView);
        this.listLayout = this.getComponent("contentMask/content/typeScrollView/mask/listLayout", cc.Layout);
        this.copyTypeItem = new FanTypeView(this.getChild("contentMask/content/typeScrollView/mask/listLayout/typeNode"));
        this.copyTypeItem.active = false;
        this.winTypeSp = this.getComponent("contentMask/content/winType", cc.Sprite);
        this.initWinMjList();

        this.hideBtn = this.getChild("hideBtn");
        this.upIcon = this.getChild("hideBtn/up");
        this.downIcon = this.getChild("hideBtn/down");
        ErmjGameConst.addCommonClick(this.hideBtn, "", this.hideBtnClick, this);
        this.rawPos = this.contentNode.position;
    }

    private initWinMjList(){
        this.winMjListNode = this.getChild("contentMask/content/winMjList");
        let copyMjNode = this.getChild("contentMask/content/winMjList/winMjView");
        let view = new WinMjItemView(copyMjNode);
        view.active = true;
        this.winMjList.push(view);
        let maxCount = this.Define.HandMjMaxCount + 4;     // 最多4个杠 多4张
        for (let i= 0; i< maxCount; i++){
            let node = <cc.Node>cc.instantiate(copyMjNode);
            node.setParent(this.winMjListNode);
            let view = new WinMjItemView(node);
            view.active = true;
            this.winMjList.push(view);
        }
    }

    protected onOpen(){
        this.clickLimit = false;
        this.hideBtn.active = false;
        // 强制显示结算面板
        this.isPressed = false;
        this.showContent(false);

        this.contentNode.opacity = 1;
        this.contentNode.runAction(cc.sequence([
            cc.fadeTo(0.5, 255),
            cc.callFunc(()=>{
                this.hideBtn.active = true;
            }, this)
        ]));
        this.actionNode.active = false;
        this.delayShowActionBtn(5000);         // 以防没收到服务器协议情况
    }

    /** 等待多少ms后显示退出和继续按钮 等待服务器清桌 */
    public delayShowActionBtn(delay: number){
        this.timer = setTimeout(() => {
            this.showActionBtn(true); 
         }, delay);
    }

    public showActionBtn(flag: boolean){
        this.actionNode.active = flag;
        if (flag){
            this.actionNode.opacity = 1;
            this.actionNode.stopAllActions();
            this.actionNode.runAction(cc.fadeTo(0.2, 255));
        }
        clearTimeout(this.timer);
    }

    protected onClose(){
        this.contentNode.stopAllActions();
        this.contentNode.opacity = 255;
        clearTimeout(this.timer);
    }

    private hideBtnClick(){
        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.ButtonClick, true);
        this.showContent(true);
    }

    private showContent(isTween: boolean = true){
        if (this.isPressed) {
            const offset = cc.v3(0, 600);
            this.isPressed = false;
            this.upIcon.active = false;
            this.downIcon.active = true;
            this.contentNode.stopAllActions();
            
            this.maskNode.opacity = 50;
            if (isTween){
                this.contentNode.runAction(cc.moveBy(0.2, cc.v2(offset.x, offset.y)).easing(cc.easeIn(1)));
            }
            else{
                this.contentNode.setPosition(this.rawPos.add(offset));
            }
        } else {
            this.isPressed = true;
            this.upIcon.active = true;
            this.downIcon.active = false;
            this.contentNode.active = true;
            this.contentNode.stopAllActions();
            this.maskNode.opacity = 153;
            if (isTween){
                this.contentNode.runAction(cc.moveTo(0.2, cc.v2(this.rawPos.x, this.rawPos.y)).easing(cc.easeOut(1)));
            }
            else{
                this.contentNode.setPosition(this.rawPos);
            }
        }
    }

    private onContinueClick(){
        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.ContinueGame, true);
        if (this.clickLimit)
            return;
        ErmjDriver.instance.reMatchPlayer();
        this.clickLimit = true;
        Game.Component.scheduleOnce(()=>{
            this.clickLimit = false;
        }, 3);
    }

    private onLeaveClick(){
        ErmjGameConst.playSound(ErmjAudioConst.commonAudio.ButtonClick, true);
        Game.Server.send(this.Define.CmdLeave, { "IsClose": 1 });
        ErmjDriver.instance.leaveGame();
    }

    public showSettleInfo(isWin: boolean, award: number, fan: number, details: any[], all_hand_cards: any){
        this.winInfoView.active = isWin;
        this.loseInfoView.active = !isWin;
        if (isWin){
            ErmjGameConst.playSound(ErmjAudioConst.commonAudio.RewardWin, true);
            this.winInfoView.showInfo(fan, award);
        }
        else{
            ErmjGameConst.playSound(ErmjAudioConst.commonAudio.RewardLose, true);
            this.loseInfoView.showInfo(fan, award);
        }
        for(let chair in details){        
            let info = details[chair];
            if (info){
                this.showFanList(info.fan_detail);
                this.showWinCardsList(all_hand_cards[chair], info.block_list || []);
                return;     // 二人麻将就一个赢家
            }
        }
    }

    public setWinType(type: number){
        let str = type == 1 ? ErmjGameConst.StateSpStrCfg.WinAll : type == 2 ? ErmjGameConst.StateSpStrCfg.Win : ErmjGameConst.StateSpStrCfg.RobWin;
        Global.ResourceManager.loadBundleAutoAtlas(ErmjGameConst.Gid, this.winTypeSp, ErmjPathHelper.gameTexturePath + "dynamic/atlas_dynamic", str, null, true);
    }

    private showFanList(fanDetail: any){
        if (!fanDetail)
            return;
        for(let key in fanDetail){
            let config = ErmjRuleConst.cardTypeConfig[key] || {};
            let item = this.getOneTypeItem();
            item.active = true;
            this.nextIndex += 1;
            item.setContext(config.name, fanDetail[key]);
        }
        this.typeScrollView.scrollToTop(0);
    }

    /**
     * 展示赢麻将
     * @param userCards 胡手上牌    加上吃碰杠14张
     * @param operList 吃碰杠数组 可能为null
     */
    private showWinCardsList(userCards: number[], operList: any[] = []){
        if (!userCards)
            return;
        let start = cc.Vec3.ZERO;
        const offset = cc.v3(62, 0);
        const spaceOffset = cc.v3(15, 0);
        const kongLastOffset = cc.v3(0, 22);
        let mjIndex = 0;
        // 吃碰杠展示
        operList.forEach(info=>{
            let all_cards: number[] = info.all_cards || [];
            all_cards.forEach((value, index)=>{
                let mjItem = this.winMjList[mjIndex];
                if (mjItem){
                    mjItem.active = true;
                    mjItem.mahjongValue = value;
                    if (info.type == 6 && index != 3)       // 暗杠并且不是第四张
                        mjItem.isFront = false;
                    else
                        mjItem.isFront = true;
                    if ((info.type == 4 || info.type == 5 || info.type == 6) && index == 3){        // 杠牌第四张 堆到前两张上面
                        let tempPos = start.sub(offset.mul(2)).add(kongLastOffset);
                        mjItem.node.setPosition(tempPos);
                    }
                    else{
                        mjItem.node.setPosition(start);
                        start = start.add(offset);
                    }
                }
                mjIndex ++;
            })
            start = start.add(spaceOffset);
        })
        // 手牌展示
        userCards.forEach((value, index)=>{
            let mjItem = this.winMjList[mjIndex];
            if (mjItem){
                mjItem.active = true;
                mjItem.mahjongValue = value;
                mjItem.isFront = true;
                if (index == userCards.length - 1){
                    mjItem.node.setPosition(start.add(spaceOffset));    // 最后一张是胡牌, 隔开
                }
                else{
                    mjItem.node.setPosition(start);
                }
                start = start.add(offset);
            }
            mjIndex ++;
        })
        // 隐藏剩余未使用牌
        if(mjIndex < this.winMjList.length){
            for(let i = mjIndex; i < this.winMjList.length; i++){
                this.winMjList[i].active = false;
            }
        }
    }

    private getOneTypeItem(){
        if (!this.typeItemList[this.nextIndex]){
            let node = <cc.Node>cc.instantiate(this.copyTypeItem.node);
            node.setParent(this.listLayout.node);
            let view = new FanTypeView(node);
            this.typeItemList.push(view);
        }
        return this.typeItemList[this.nextIndex];
    }

    private hideAllTypeItem(){
        this.typeItemList.forEach(item=>{
            item.active = false;
        })
    }

    public clearByRound(){
        this.hideAllTypeItem();
        this.nextIndex = 0;
        this.winInfoView.active = false;
        this.loseInfoView.active = false;
        this.active = false;
    }
}

class WinInfoView extends ErmjBaseView{
    private multLbl: cc.Label;
    private winLbl: cc.Label;
    private effectSk: sp.Skeleton;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.multLbl = this.getComponent("multLbl", cc.Label);
        this.winLbl = this.getComponent("winLbl", cc.Label);
        this.effectSk = this.getComponent("effect", sp.Skeleton);
    }

    public showInfo(nMult: number, nWin: number){
        this.multLbl.string = nMult.toString();
        this.winLbl.string = Global.Toolkit.formatPointStr(nWin, false, true);
        this.effectSk.node.active = true;
        this.effectSk.setAnimation(0, "idle", false);
        this.effectSk.addAnimation(0, "idle2", true, 1);
    }

    protected onClose(){
        this.effectSk.node.active = false;
        this.effectSk.clearTracks();
    }
}

