class FanTypeView extends ErmjBaseView{
    private typeLbl: cc.Label;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.typeLbl = this.getComponent("typeLbl", cc.Label);
    }

    public setContext(name: string, nMult: number){
        this.typeLbl.string = `${name}  ${nMult}番`;
    }
}

class WinMjItemView extends ErmjBaseView{
    private _value: number;
    private frontBg: cc.Sprite;
    private frontSp: cc.Sprite;
    private backBg: cc.Sprite;
    
    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.frontBg = this.getComponent("bg", cc.Sprite);
        this.frontSp = this.getComponent("front", cc.Sprite);
        this.backBg = this.getComponent("back", cc.Sprite);
    }

    /** 设置牌值和图案样式 */
    public set mahjongValue(value: number){
        if (this._value == value)
            return;
        this._value = value;
        Global.ResourceManager.loadBundleAutoAtlas(ErmjGameConst.Gid, this.frontSp, ErmjPathHelper.gameTexturePath + "mahjong/atlas_mahjong", ErmjMjStyleHelper.mjHandMap[value], null, true);
        if (cc.sys.isBrowser)
            this.node.name = String(value);
    }

    public get mahjongValue(){
        return this._value;
    }

    /** 设置麻将正反面 */
    public set isFront(flag: boolean){
        this.frontBg.node.active = flag;
        this.frontSp.node.active = flag;
        this.backBg.node.active = !flag;
    }

    /** 设置正面背景特殊色提示 */
    public setSpecialColor(flag: boolean){
        this.frontBg.node.color = flag && ErmjMjStyleHelper.SpecialBgColor || ErmjMjStyleHelper.NormalBgColor;
    }
}